@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Maven+Pro:wght@400;500;600;700;800;900&family=MuseoModerno:wght@200;300;400;500;600;700;800;900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto+Mono:wght@200&display=swap');

body{
    padding: 0;
    margin: 0;
}

.container-artikel .artikel-title p{
    position: absolute;
    color: #333;
    font-family: 'Maven Pro', sans-serif;
    font-weight: 700;
    font-size: 24px;
    margin-top: 2080px;
    margin-left: 415px;
}

.container-artikel .artikel-title a{
    font-family: 'Maven Pro', sans-serif;
    font-weight: 700;
    position: absolute;
    text-decoration: none;
    background-color: white;
    font-size: 0.9rem;
    margin-top: 2080px;
    margin-left: 1380px;
    color: #812ba6;
    border-right: 1px solid #A539D4;
    border-left: 1px solid #A539D4;
    border-bottom: 1px solid #A539D4;
    border-top: 1px solid #A539D4;
    border-radius: 10px;
    padding: 10px 18px 10px 18px;
    border: 1px solid rgba(123, 31, 161, 0.5);
    border-top-width: 1px;
    border-right-width: 1px;
    border-bottom-width: 1px;
    border-left-width: 1px;
    border-top-style: solid;
    border-right-style: solid;
    border-bottom-style: solid;
    border-left-style: solid;
    border-top-color: rgba(123, 31, 161, 0.5);
    border-right-color: rgba(123, 31, 161, 0.5);
    border-bottom-color: rgba(123, 31, 161, 0.5);
    border-left-color: rgba(123, 31, 161, 0.5);
    border-image-source: initial;
    border-image-slice: initial;
    border-image-width: initial;
    border-image-outset: initial;
    border-image-repeat: initial;
}

.container-artikel .artikel-title a:hover{
    border-color: #A539D4;
    transition: .5s;
    background-color: #a539d40b;
}

.container-artikel .container-image ul{
    display: flex;
    list-style: none;
    margin-left: 355px;
    position: absolute;
    margin-top: 2150px;
}

.container-artikel .container-image ul li img{
    cursor: pointer;
    margin-left: 20px;
}

.container-artikel .container-image ul li a{
    text-decoration: none;
    color: #333;
    font-size: 0.75rem;
    font-weight: 500;
    font-family: 'Maven Pro', sans-serif;
    cursor: pointer;
} 

.container-artikel .container-image ul li p{
    font-family: 'Maven Pro', sans-serif;
    margin-left: 18px;
    font-size: 0.75rem;
    font-weight: 500;
    color: gray;
    cursor: pointer;
}

.container-artikel .container-image ul li h5{
    height: 60px;
    font-size: 1.25rem;
    margin-top: 5px;
    font-weight: 700 !important;
    margin-left: 18px;
    line-height: 1.5;
    font-family: 'Maven Pro', sans-serif;
    font-weight: 500;
    cursor: pointer;
}

.container-artikel .container-image ul li h4{
    display: flex;
    border: 1px solid #D1D1D1;
    padding: 4px 16px;
    font-size: 10px;
    font-weight: 500;
    border-radius: 20px;
    position: absolute;
    color: #333;
    margin-top:-17px;
    margin-left: 17px;
    cursor: pointer;
}

.container-artikel .container-image ul li h3{
    border: 1px solid #D1D1D1;
    padding: 4px 16px;
    font-size: 10px;
    font-weight: 500;
    border-radius: 20px;
    display: flex;
    position: absolute;
    margin-left: 75px;
    margin-top:-17px;
    color: #333;
    cursor: pointer;
}

.container-artikel .container-image .h3{
    border: 1px solid #D1D1D1;
    padding: 4px 16px;
    font-size: 10px;
    font-weight: 500;
    border-radius: 20px;
    display: flex;
    position: absolute;
    margin-left: 85px;
    margin-top:-17px;
    color: #333;
}
