@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Maven+Pro:wght@400;500;600;700;800;900&family=MuseoModerno:wght@200;300;400;500;600;700;800;900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto+Mono:wght@200&display=swap');

body{
    padding: 0;
    margin: 0;
}

.Container-Smart .teks p{
    color: #333;
    font-size: 25px;
    margin-top: 480px;
    margin-left: 400px;
    font-family: 'Maven Pro', sans-serif;
    font-weight: 600;
    position: absolute;
}

.Container-Smart .see-more a{
    font-family: 'Maven Pro', sans-serif;
    font-weight: 700;
    position: absolute;
    text-decoration: none;
    background-color: white;
    font-size: 0.9rem;
    margin-top: 480px;
    margin-left: 73%;
    color: #812ba6;
    padding-top: 1px;
    border-right: 1px solid #A539D4;
    border-left: 1px solid #A539D4;
    border-bottom: 1px solid #A539D4;
    border-top: 1px solid #A539D4;
    border-radius: 10px;
    padding: 7px 20px 10px 20px;
    border: 1px solid rgba(123, 31, 161, 0.5);
    border-top-width: 1px;
    border-right-width: 1px;
    border-bottom-width: 1px;
    border-left-width: 1px;
    border-top-style: solid;
    border-right-style: solid;
    border-bottom-style: solid;
    border-left-style: solid;
    border-top-color: rgba(123, 31, 161, 0.5);
    border-right-color: rgba(123, 31, 161, 0.5);
    border-bottom-color: rgba(123, 31, 161, 0.5);
    border-left-color: rgba(123, 31, 161, 0.5);
    border-image-source: initial;
    border-image-slice: initial;
    border-image-width: initial;
    border-image-outset: initial;
    border-image-repeat: initial;
}

.Container-Smart .see-more a:hover{
    border-color: #A539D4;
    transition: .5s;
    background-color: #a539d40b;
}

.Container-Smart .content-card{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: absolute;
    margin-top: 530px ;
    margin-left: 350px;
}

.Container-Smart .content-card ul{
    list-style: none;
    display: flex;
}

.Container-Smart .content-card ul li img{
    margin-left: 10px;
    background-color: black;
}
