@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Maven+Pro:wght@400;500;600;700;800;900&family=MuseoModerno:wght@200;300;400;500;600;700;800;900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto+Mono:wght@200&display=swap');

body {
    margin: 0;
    padding: 0;
    
}


/* navbar bagian link */
.containerNav {
    background-color: #7C32A1;
    height: 87px;
    padding: 15px 0px 10px 0;
    position: relative;
    box-shadow: 0px 2px 4px -1px rgb(0 0 0 / 20%), 0px 4px 5px 0px rgb(0 0 0 / 14%), 0px 1px 10px 0px rgb(0 0 0 / 12%);
    overflow-x: clip;
    text-align: left;
    border-bottom: 3px solid #FFC007;
}

.containerNav .header-bg img{
    margin-left: 1375px;
    margin-top: -15px;
    position: absolute;
}

.containerNav .image img{
    cursor: pointer;
    display: flex;
    margin-left:380px;
    margin-top: 36px;
}

.containerNav .Navbar{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    margin-left: 338px;
    margin-top: -100px;
}

.containerNav .Navbar ul{
    list-style: none;
    display: flex;
}

.containerNav .Navbar ul li a{
    text-decoration: none;
    margin-left: 20px;
    color: white;
    font-family: 'Maven Pro', sans-serif;
    font-weight: 500;
    font-size: 0.75rem;
    background-color:#A539D4;
    padding-right: 14px;
    padding-left: 14px;
    padding-top: 8px;
    padding-bottom: 8px;
    border-radius: 10px;
}

.containerNav .Navbar ul li a:hover{
    color: #A539D4;
   background-color: white; 
   transition: .5s;
}

.containerNav .Navbar ul li a:focus{
    color: #A539D4;
   background-color: white; 
}

.containerNav .Navbar2{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    margin-left: 450px;
    margin-top: -50px;
}

.containerNav .Navbar2 ul{
    list-style: none;
    display: flex;
}

.containerNav .Navbar2 ul li a{
    text-decoration: none;
    margin-left: 40px;
    color: white;
    font-family: 'Maven Pro', sans-serif;
    font-weight: 500;
    font-size: 0.88rem;
}

.containerNav .Navbar2 ul li a:active{
    color: #FFC007;
}

/* navbar bagian masuk/daftar */

.containerNav .login a{
    text-decoration: none;
    background-color: rgba(255, 255, 255, 0.693);
    position: absolute;
    display: flex;
    margin-left: 73%;
    margin-top: -35px;
    font-family: 'Maven Pro', sans-serif;
    font-weight: 500;
    font-size: 0.75rem;
    text-transform: capitalize;
    color: #7B1FA1;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 7px;
    padding-top: 7px;
    border-radius: 10px;
    box-shadow: 1px 2px 3px rgb(28, 28, 28);
    cursor: pointer;
}

.containerNav .login a:hover{
    transition: .5s;
    background-color: white;
    box-shadow: 1px 3px 3px rgb(28, 28, 28);
}

.containerNav .search img{
    position: absolute;
    margin-left: 1296px;
    margin-top: -32px;
    cursor: pointer;
}

.containerNav .search img:hover{
    background-color: #7a1fa1bf;
    border-radius: 10px;
}

.containerNav .download img{
    position: absolute;
    margin-left: 1345px;
    margin-top: -30px;
    cursor: pointer;
    transition: .5s;    
}

.containerNav .download img:hover{
    background-color: #7a1fa1bf;
    border-radius: 5px;
    transition: .5s;
    cursor: pointer;
}
