.container-main .Daftar-yuk a{
    color: #7B1FA1;
    font-family: 'Maven Pro', sans-serif;
    font-weight: 700;
    text-decoration: none;
    position: absolute;
    margin-left: 400px;
    margin-top: 300px;
    padding-top: 13px;
    padding-bottom: 13px;
    padding-right: 25px;
    padding-left: 25px;
